            />
          </div>
          <div class="input-set">
            <div class="label-box"></div>
          </div>
          <div class="input-set">
            <div class="label-box">
              <p class="label">Design Pressure (psi):</p>
              <!-- <label class="star-label">
                <i class="las la-asterisk"></i>
              </label>-->
            </div>
            <input
              type="number"
