              <!-- <label class="star-label">
                <i class="las la-asterisk"></i>
              </label>-->
            </div>
            <input
              type="number"
              v-model="formData.design_temp_max_f"
              placeholder="Max Design Temperature (°F)"
              @input="convertToCelsius_MAX"
            />
          </div>
          <div class="input-set">
            <div class="label-box">
              <p class="label">Min Design Temperature (°F):</p>
              <!-- <label class="star-label">
                <i class="las la-asterisk"></i>
              </label>-->
            </div>
            <input
              type="number"
