<script>
import axios from "/axios.js";
import moment from "moment";
import DxSelectBox from "devextreme-vue/select-box";
import DxDateBox from "devextreme-vue/date-box";
// import { DxLookup, DxDropDownOptions } from "devextreme-vue/lookup";
import contentLoading from "@/components/app-structures/app-content-loading.vue";

export default {
  name: "popup-add",
  components: {
    DxSelectBox,
    DxDateBox,
    // DxLookup,
    // DxDropDownOptions,
    contentLoading
  },
  data() {
    return {
      formData: {},
      formSelect: {
        site: [],
        service: [],
        service_class: [],
        pipe_spec: [],
        material: [],
        piping_group: [],
        system: [],
        inventory_group: []
      }
    };
  },
  computed: {
    all_form_select_loaded() {
      if (
        this.formSelect.site.length > 0 &&
        this.formSelect.service.length > 0 &&
        this.formSelect.pipe_spec.length > 0 &&
        this.formSelect.material.length > 0 &&
        this.formSelect.inventory_group.length > 0 &&
        this.formSelect.system.length > 0 &&
        this.formSelect.piping_group.length > 0
      ) {
        return true;
      } else return false;
    }
  },

  watch: {
    "formData.operating_pressure_barg"(value) {
      this.formData.operating_pressure_psi = value * 14.5038;
    },
    "formData.operating_pressure_psi"(value) {
      this.formData.operating_pressure_barg = value / 14.5038;
    },
    "formData.design_pressure_barg"(value) {
      this.formData.design_pressure_psi = value * 14.5038;
    },
    "formData.design_pressure_psi"(value) {
      this.formData.design_pressure_barg = value / 14.5038;
    }
  },

  created() {
    this.FETCH_DROPDOWN();
  },
  methods: {
    SAVE() {
      this.formData.inservice_date = moment(
        this.formData.inservice_date
      ).format("L");
      //console.log(this.formData);
      if (
        this.formData.line_no &&
        this.formData.id_site &&
        // this.formData.line_from &&
        // this.formData.line_to &&
        // this.formData.pid_no &&
        // this.formData.drawing_no &&
        // this.formData.inservice_date &&
        // this.formData.piping_code &&
        this.formData.pipe_spec &&
        this.formData.nps &&
        // this.formData.stress &&
        // this.formData.E &&
        // this.formData.design_pressure_psi &&
        // this.formData.operating_pressure_psi &&
        // this.formData.design_temp_c &&
        // this.formData.operating_temp_c &&
        // this.formData.deadleg &&
        // this.formData.insulation &&
        // this.formData.insulation_type &&
        // this.formData.insulation_size &&
        this.formData.CAd &&
        this.formData.Ld !== "" &&
        this.formData.id_service
        // this.formData.id_material
      ) {
        this.$ons.notification.confirm("Confirm save?").then(res => {
          if (res == 1) {
            const user = JSON.parse(localStorage.getItem("user"));
            this.formData.created_by = user.id;
            this.formData.updated_by = user.id;
            this.formData.is_active = 1;
            const data = this.formData;
            axios({
              method: "get",
              url:
                "/master-data/get-md-pipespec-by-nps?spec_class=" +
                data.spec_class +
                "&nps=" +
                data.nps,
              headers: {
                Authorization:
                  "Bearer " + JSON.parse(localStorage.getItem("token"))
              }
            }).then(res => {
              if (res.status == 200) {
                this.formData.id_spec = res.data.id;
                this.CREATE_PIPING();
              }
            });
          }
        });
      } else {
        this.$ons.notification.alert("Please fill all required fields.");
      }
    },
    CANCEL() {
      if (this.formData != this.formData) {
        this.$ons.notification
          .confirm("Your unsaved changes will be lost")
          .then(res => {
            if (res == 1) {
              this.$emit("closePopup");
            }
          });
      } else {
        this.$emit("closePopup");
      }
    },
    CREATE_PIPING() {
      console.log(this.formData);
      axios({
        method: "post",
        url: "/FlowlineInfo",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: this.formData
      })
        .then(res => {
          // console.log(res.data[0]);
          if (res.status == 201) {
            this.$emit("closePopup");
            const id_tag = res.data.id_tag;
            if (id_tag) {
              this.$router.push("/flowline/tag" + id_tag + "/info");
            }
          }
        })
        .catch(error => {
          this.$ons.notification.alert(error.code + " " + error.message);
        })
        .finally(() => {});
    },
    FETCH_DROPDOWN() {
      this.FETCH_DROPDOWN_SITE();
      this.FETCH_DROPDOWN_MATERIAL();
      this.FETCH_DROPDOWN_PIPE_SPEC();
      this.FETCH_DROPDOWN_PIPEGROUP();
      this.FETCH_DROPDOWN_SYSTEM();
      this.FETCH_DROPDOWN_INVENTORY_GROUP();
      this.FETCH_DROPDOWN_SERVICE_CLASS();
      this.FETCH_DROPDOWN_SERVICE();
    },
    FETCH_DROPDOWN_INVENTORY_GROUP() {
      axios({
        method: "get",
        url: "/master-data/get-md-invetorygroup-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.formSelect.inventory_group = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_DROPDOWN_SERVICE_CLASS() {
      axios({
        method: "get",
        url: "/master-data/get-md-service-class-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.formSelect.service_class = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_DROPDOWN_SYSTEM() {
      axios({
        method: "get",
        url: "/master-data/get-md-system-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.formSelect.system = res.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_DROPDOWN_MATERIAL() {
      axios({
        method: "get",
        url: "/master-data/get-md-pipematerial-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.material = res.data;
        }
      });
    },
    FETCH_DROPDOWN_PIPE_SPEC() {
      axios({
        method: "get",
        url: "/master-data/get-md-pipespec-for-dd",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.pipe_spec = res.data;
        }
      });
    },
    FETCH_DROPDOWN_PIPEGROUP() {
      axios({
        method: "get",
        url: "/master-data/get-md-pipinggroup-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.piping_group = res.data;
        }
      });
    },
    FETCH_DROPDOWN_SERVICE() {
      axios({
        method: "get",
        url: "/master-data/get-md-service-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.service = res.data;
        }
      });
    },
    FETCH_DROPDOWN_SITE() {
      axios({
        method: "get",
        url: "/master-data/get-md-site-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.site = res.data;
        }
      });
    },
    convertToFahrenheit() {
      if (this.formData.operating_temp_c) {
        this.formData.operating_temp_f =
          (this.formData.operating_temp_c * 9) / 5 + 32;
      } else {
        this.formData.operating_temp_f = null;
      }
    },
    convertToCelsius() {
      if (this.formData.operating_temp_f) {
        this.formData.operating_temp_c =
          ((this.formData.operating_temp_f - 32) * 5) / 9;
      } else {
        this.formData.operating_temp_c = null;
      }
    },
    convertToFahrenheit_MAX() {
      if (this.formData.design_temp_max_c) {
        this.formData.design_temp_max_f =
          (this.formData.design_temp_max_c * 9) / 5 + 32;
      } else {
        this.formData.design_temp_max_f = null;
      }
    },
    convertToCelsius_MAX() {
      if (this.formData.design_temp_max_f) {
        this.formData.design_temp_max_c =
          ((this.formData.design_temp_max_c - 32) * 5) / 9;
      } else {
        this.formData.design_temp_max_c = null;
      }
    },
    convertToFahrenheit_MIN() {
      if (this.formData.design_temp_min_c) {
        this.formData.design_temp_min_f =
          (this.formData.design_temp_min_c * 9) / 5 + 32;
      } else {
        this.formData.design_temp_min_f = null;
      }
    },
    convertToCelsius_MIN() {
      if (this.formData.design_temp_min_f) {
        this.formData.design_temp_min_c =
          ((this.formData.design_temp_min_c - 32) * 5) / 9;
      } else {
        this.formData.design_temp_min_c = null;
      }
    }
  }
};
</script>

